        </h2>
        <p>毎日を元気に過ごしたい。体も心も健康で強くありたい。<br>そのためには、野菜の栄養が必要です。<br>忙しい毎日、カロリーは取っていてもきちんと栄養まで足りているでしょうか。<br>毎日の食事で、摂れる野菜の種類や量、栄養には限界があります。<br> Why Juice?は、現代人に必要な「バランスのとれた食生活」を、<br>Easy&amp;Fastなアプローチで解決するジュースブランドです。<br>１日に必要な野菜の栄養を、おいしく簡単で、<br>Reasonable（理にかなった方法、プライス）にかなえてくれる<br>アイテムを揃えています。</p>
