        </div>
        <h4><span>安心・安全なこだわりのジュース製法</span></h4>
        <p>>Why Juice?では鮮度・産地・製法にこだわった<br>「中身の見える」安心で安全なジュースを製造・販売しています。<br>栄養がダイレクトに体に届くように、全国各地から厳選した野菜やフルーツを<br>ひとつひとつ時間をかけて圧搾し、<br>保存料や食品添加物を一切使用しないジュースです。<br /><br />Cold Pressed Juiceは、栄養素を壊すことなく生きたまま抽出する<br>「コールドプレス製法」を用いています。<br>「コールドプレスジュース」は、パルプ（果皮などの固形物）を取り除いたクリアな液体で、<br>すっきりと飲みやすいのが特長。<br>胃に負担が少なく、消化しやすい、デトックスにもパーフェクトなドリンクです。<br>Smoothieは、野菜とフルーツを丸ごとミキサーでブレンドしたドリンクで、<br>食物繊維を豊富に摂取できます。<br>アーモンドミルクをベースにしたスムージーは、ミルクシェイクのようなヘルシースイーツです。<br
          /><br />Long Life Juiceは、安心・安全な野菜ジュースを<br>より多くの方に気楽に手軽に飲んでいただきたい。<br>それを実現するために低温殺菌し、１年常温保存できるジュースにしました。<br>なるべくパルプを除去した、のどごしがいい飲みやすいジュースです。<br>新潟の山あいにある工場で、手仕事により製造しています。</p>
      </div>
