<script>
export default{
mounted(){
  $(window).scroll(function(event){
  		var winPos = $(window).scrollTop();
  		console.log(winPos)
  		if(winPos>500){
  			$('.Detail').css({opacity:"1"});
  		}else{
  			$('.Detail').css({opacity:"0"});
  		}
  		if(winPos>1300){
  			$('.Detail').eq(1).css({opacity:"1"});
  		}else{
  			$('.Detail').eq(1).css({opacity:"0"});
  		}

  		if(winPos>900){
