     j = 0;
   }
 }, 2000)


 $("#bt-next").click(function() {
   if (x >= 0 && n <= ($("#four").find("li").length - 1)) {
     console.log("现在的X" + x)
     $("body,html").animate({
       scrollTop: (x + 1) * $(window).height()
     }, 500)
     setTimeout(function() {
       t = document.documentElement.scrollTop || document.body.scrollTop;
       h = $(window).height();
       x = parseInt(t / h);
     }, 600)
   }

 })
 $("#bt-prev").click(function() {
   if (x > 0 && n <= ($("#four").find("li").length - 1)) {
     // console.log("现在的X"+x)
     $("body,html").animate({
       scrollTop: (x - 1) * $(window).height()
     }, 500)
     setTimeout(function() {
       t = document.documentElement.scrollTop || document.body.scrollTop;
       h = $(window).height();
       x = parseInt(t / h);
     }, 600)
   }
 })
 $(".li").mouseover(function() {
   $(this).find(".bg1").addClass("bg2").next("p").addClass("p1");
   $("#four").find(".li").mouseleave(function() {
     if ($(this).attr("name") != "xuanzhong") {
       console.log("触发了未选中")
       $(this).find(".bg1").removeClass("bg2").next("p").removeClass("p1")
     }
   })
 })
 $(".li").click(function() {
   $("html,body").animate({
     scrollTop: ($(this).index() + 3) * h
   })
 })



 let t = document.documentElement.scrollTop || document.body.scrollTop;
 let h = $(window).height();
 let x = parseInt(t / h);
 let y = Math.round(t / h);
 let m = t - x * $(window).height();
 let n = x - ($(".screen").length - 1);

 //已经滚过去的screen加上fixed样式，并让他们固定在屏幕上，作为最底层显示，margintopTop清除掉
 $(".screen").eq(x).css({
   position: "fixed",
   top: 0,
   left: 0,
   zIndex: "-100",
   marginTop: 0
 });

 //目前在屏幕中间的视图前面所有视图固定在屏幕上
 $(".screen").eq(x).prevAll(".screen").css({
   position: "fixed",
   top: 0,
   left: 0,
   zIndex: "-100",
   marginTop: 0
 });

 //为正在升上屏幕的视图加一个marginTop来模拟上方的元素占位
 $(".screen").eq(x + 1).css({
   marginTop: $(window).height()
 })

 //固定在屏幕上的视图的内容显示
 $(".screen").eq(x).find("h2,p,img").addClass("show")

 //#four的所有图片给上屏幕的高
 $("#four").find("img").css({
   height: $(window).height() + "px"
 });

 //给每一个li标签加上一个递减的zindex属性，让前面的图片遮住后面的
 $("#four").find("li").each(function(a, b) {
   $(this).css({
     zIndex: 11 - a
   })
 })

 //每一个包裹着图片的window也给上屏幕高度
 $("#four").find(".window").css({
   height: $(window).height() + "px"
 })

 //屏幕滚动时触发的事件
window.onscroll=shang;
function shang() {
   // 每次滚动重新获取数据
   t = document.documentElement.scrollTop || document.body.scrollTop; //t是滚动条滚动的高度
   h = $(window).height()
   x = parseInt(t / h);
   y = Math.round(t / h);
   //m是从第一个li算起来现在的顶距
   m = t - x * $(window).height();

   //当前上方li的下标
   n = x - ($(".screen").length - 1);
   console.log("x是" + x)

   //如果在第一个视图，上升箭头消失，反之出现
   if (x > 0) {
     $("#bt-prev").css({
       opacity: "1"
     })
   } else {
     $("#bt-prev").css({
       opacity: "0"
     })
   }

   //前三个标准视图时的事件
   if (x >= 0 && x <= 3) {

     //当前屏幕上完全滚动完毕的视图给上fixed，给一个递增的zindex属性
     $(".screen").eq(x).css({
       position: "fixed",
       top: 0,
       left: 0,
       marginTop: 0,
       zIndex: -100 + x
     });

     //下一块视图给relative，margintop给（x+1）个屏幕高度的值模拟上方元素占位，同时其他视图的margintop清除
     $(".screen").eq(x + 1).css({
       position: "relative",
       marginTop: h * (x + 1)
     }).siblings(".screen").css({
       marginTop: 0
     })

     //当前滚动完毕的视图内容显示
     $(".screen").eq(x).find("h2,p,img,a").addClass("show");

     //下一个视图滚动到一半时让上一块视图（目前固定的视图）内容消失
     $(".screen").eq(y).prevAll(".screen").find("h2,p,img,a").removeClass("show");
   }

   if (x >= 3 && n <= ($("#four").find("li").length)) {
     //给当前圆点加上颜色
     $("#four").find(".li").find(".bg1").removeClass("bg2").next("p").removeClass("p1")
     $("#four").find(".li").attr("name", " ")

     $("#four").find(".li").eq(Math.round(t / h) - 3).find(".bg1").addClass("bg2").next("p").addClass("p1")
     $("#four").find(".li").eq(Math.round(t / h) - 3).attr("name", "xuanzhong")
   }


   //视图在#four中的图片时的事件
   if (x >= 3 && n <= ($("#four").find("li").length - 1)) {

     //当视图超过第一张图片时触发的事件
     if (x >= 4) {

       //所有window元素给zindex属性，高度给屏幕高
       $("#four").find(".window").css({
         zIndex: 0,
         height: $(window).height() + "px"
       })
     }

     //所有li元素给absolute，叠在一起，并让它们铺在底部
     $("#four").find("li").css({
       position: "absolute",
       bottom: 0,
       left: 0
     })

     //因为已经进入#four，所以所有视图都给fixed钉在屏幕上
     $(".screen").css({
       position: "fixed"
     })

     //前四块视图全部定位fixed，不占位置，第五块给足够的marginTop模拟占位
     $("#four").next("#five").css({
       marginTop: h * ($(".screen").find("li").length + $(".screen").length - 1) + "px"
     })




     // console.log("n为" + n)
     // console.log("t/h为" + t / h)


     if (n >= 0) {

       //当前屏幕内正在向上移动的li的高度等于m的值，给递增的zindex值使后面的图片能覆盖掉前面的图片
       $("#four").find("li").eq(n + 1).css({
         height: m + "px",
         zIndex: 11 + n
       })

       //正在向上移动的li的所有前面的li给100%的高度，防止滚动不足图片没有完全展开
       $("#four").find("li").eq(n + 1).prevAll("li").css({
         height: "100%"
       })

       //正在向上移动的li的所有后面的li给0高度
       $("#four").find("li").eq(n + 1).nextAll("li").css({
         height: "0"
       })
     }


     $("#four").children("section").css({
       marginTop: 0
     })
   }

   //n是li下标，当下标大于等于最后一个li的下标时执行的内容，即页面显示到最后一张li时执行
   if (n >= ($("#four").find("li").length - 1)) {
     // $("#five").css({marginTop:$(window).height()})

     //所有li加高度为屏幕高度
     $("#four").find("li").css({
       height: $(window).height()
     })
   }

   //
   if (n >= ($("#four").find("li").length - 1)) {


     $("#four").children("section").css({
       marginTop: ($("#four").find("li").length - 1) * h + "px"
     })

     //
     $("#five").css({
       marginTop: ($(".screen").find("li").length - 1) * $(window).height() + "px"
     })
     $("#four").find("li").css({
       position: "relative"
     });
     $(".screen").css({
       position: "relative"
     })

