import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import because from '@/components/Because'
import Cold from '@/components/ColdPressedjuice'
import Detox from '@/components/Detox'
import long from '@/components/long'
import wrap from '@/components/wrap'
import oem from '@/components/OEM'

import community from '@/components/community'

import delivery from '@/components/delivery'
import mini from '@/components/mini'

import diary from '@/components/diary'
import shop from '@/components/shop'

Vue.use(Router)

export default new Router({
  routes: [
  {
    path: '/',
    name: 'home',
    component:home
  },
  {
    path: '/Because',
    component:because
  },{
    path: '/cold',
    component:Cold
  },{
    path: '/Detox',
    component:Detox
  },{
    path: '/long',
    component:long
  },{
    path: '/wrap',
    component:wrap
  },{
    path: '/OEM',
    component:oem
  },{
    path: '/Delivery',
    component:delivery
  },{
    path: '/Community',
    component:community
  },{
    path: '/diary',
    component:diary
  },{
    path: '/shop',
    component:shop
  },{
    path: '/mini',
    component:mini
  }
  ]
})



