					<img src="../../static/myimg7/menu/menu1/img-01.jpg">
					<h4>
						Fresh, Relax, Beauty
					</h4>
					<p>Even though time is short every day, it is essential to consume the goodness of vegetables in order to maintain a healthy mind and body.<br>Why Juice? Cold-Pressed Juice is effective, while being quick and easy to consume.<br>A range of vegetable nutrients speed up your metabolism and build immunity, leading to a strong, refreshed body.</p>
<p>After fruit and vegetables are finely pulverized, they are placed in a bag and pressed.<br>The cold-pressed method extracts the maximum amount of nutrients while minimizing nutrient loss.<br>A feature of our juices is that we remove the pulp (solids such as skin) to produce a clear and refreshing drink.<br>Our juices are easy to digest and place minimum stress on the stomach, so they are perfect for detoxing.<br>We select the freshest fruit and vegetables from around the country, when nutrients are at their peak, produced without pesticides or with minimal pesticides.<br>Our freshly squeezed juice is as though you were eating whole fruits and vegetables.<br>We use no additives or preservatives, so please consume our juices on the day of purchase.</p>
			</div>

	<div class="Mm1-sec3 xuanxiang">
	<iframe width="992" height="558" src="https://www.baidu.com/" frameborder="0" allowfullscreen></iframe>
	</iframe>
	<p>Why Juice?のCold-Pressed JuiceとDetox Juice Programsはコールドプレス製法を用いて製造しています。コールドプレス製法とは、砕いた野菜を圧力でゆっくりと潰して搾汁する方法です。製造の過程で極力野菜に熱を加えずにその栄養素を壊すことなく生きたまま抽出することができます。細かい繊維のフィルタを通すため、パルプ（果皮などの固形物）が取り除かれたクリアな液体が抽出され、すっきりと飲みやすく、胃に負担が少ない、デトックスにもパーフェクトなドリンクとなります。スムージー（野菜やフルーツなどを牛乳やヨーグルト、水などと一緒にミキサーに入れてブレンドしたシェイク状の飲み物）とは異なる製法です。</p>
	</div>

