       display:"white"
     })
     $(this).find(".xs").stop(true).fadeIn(100)
   })
   $("#header1").find("ul").children("li").eq(4).mouseenter(function() {
     $(this).find("a").css({
       color:"black"
     })
     $(this).find(".xs").stop(true).fadeIn(100)
   })
   $("#header1").find("ul").children("li").eq(1).mouseleave(function(){
     $(this).find(".xs").fadeOut(100)
     // setTimeout(function(){
       $(this).find("a").css({
         color:"white"
       })
     // },100)

   })
   $("#header1").find("ul").children("li").eq(4).mouseleave(function(){
     $(this).find(".xs").fadeOut(100)
     // setTimeout(function(){
       $(this).find("a").css({
         color:"white"
       })
     // })

   })


