     $(this).find(".xs").stop(true).fadeIn(100)
   })
   $("#header2").find("ul").children("li").eq(4).mouseenter(function() {
     $(this).find(".xs").stop(true).fadeIn(100)
   })
   $("#header2").find("ul").children("li").eq(1).mouseleave(function(){
     $(this).find(".xs").fadeOut(100)
   })
