<script>
export default {
  mounted(){
    window.onscroll=function(event){
				var winPon = $(window).scrollTop();
					 if(winPon>613&&winPon<1609){
							$('#fixone').addClass("gd1")
							$('#fixone').removeClass("jd1")
							$('#fixone').removeClass("jd2")

						}else if(winPon>1609){
							$('#fixone').removeClass("gd1")
							$('#fixone').addClass("jd2")

						}else if(winPon<613){
							$('#fixone').removeClass("gd1")
							$('#fixone').addClass("jd1")

						}
							if (winPon>2222) {
					$('#fixtwo').removeClass("jd1")
					$('#fixtwo').addClass("gd1")

							}else{
								$('#fixtwo').removeClass("gd1")
					$('#fixtwo').addClass("jd1")
							}
		    };
  }
}
</script>

