<template lang="html">
  <div class="app">
    <div id="menu5-banner">
		<img src="../../static/myimg7/menu/menu5/img-hdr.jpg">
	</div>
	<div id="menu5-main">
			<h5>OEM</h5>
		<div>
			<span></span>
			<img src="http://why-juice.me/oem/imgs/text-about.gif">
		</div>
		<p>hy Juice?は、各地の野菜とフルーツを使用し、<br/>企業やイベントのコンセプトに合うオリジナルジュースをつくっています。</p>
		<div>
			<img src="../../static/myimg7/menu/menu5/img-01.jpg">
			<p>こだわりの食材を使い、丁寧な製法で、安心で美味しい製品作りを目指しています。
「こんな食材を使った商品をつくりたい」「他社との差別化を図り、会社の顔となる商品をつくりたい」「オーガニックな商品で会社のイメージアップを図りたい」など各社様のご要望をお寄せください。</p>
	<a href="#">Contact</a>
		</div>
	</div>

		<div class="menu1-footer">
      <img  :src="imgp" @mouseenter="hov1" @mouseleave="hov">

</div>
  </div>
</template>

<script>
import im1 from "../../static/myimg7/menu/menu1/bn-community_ov.jpg"
import im2 from "../../static/myimg7/menu/menu1/bn-community.jpg"
export default {
  data(){
    return{
    imgp:im2
    }
  },
  methods:{
      hov1(){
        this.imgp=im1;
      },
      hov(){
        this.imgp=im2
      }
  },
  created(){
    window.onscroll=null;
  }

}
</script>

<style scoped lang="css">
* {
  padding: 0;
  margin: 0;
  font-size: 14px; }

ul, ol {
  list-style: none; }

#menu5-banner {
  z-index: 1;
  top: 0;
  width: 100%;
  position: fixed;
  height: 40vh; }
  #menu5-banner img {
    height: 40vh;
    width: 100%; }

#menu5-main {
  position: relative;
  z-index: 10;
  background: #fff;
  width: 100%;
  margin-top: 40vh; }
  #menu5-main > div:nth-child(4) {
    border-bottom: #000000 solid 2px;
    padding-bottom: 80px;
    margin-top: 55px !important;
    width: 992px;
    margin: 0 auto; }
    #menu5-main > div:nth-child(4) > p {
      width: 574px;
      margin: 45px auto 0;
      line-height: 25px; }
    #menu5-main > div:nth-child(4) > a {
      margin-top: 20px;
      font-size: 20px;
      text-align: center;
      line-height: 1;
      color: #888;
      width: 100%;
      display: block; }
  #menu5-main > h5 {
    font-weight: normal;
    width: 100%;
    text-align: center;
    margin-bottom: 30px;
    padding-top: 70px;
    font-size: 50px;
    line-height: 1;
    text-align: center; }
  #menu5-main > div:nth-child(2) {
    width: 992px;
    margin: 0 auto;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center; }
    #menu5-main > div:nth-child(2) > span {
      width: 217px;
      display: block;
      border-bottom: #d2d2d2 solid 1px; }
    #menu5-main > div:nth-child(2) > img {
      padding: 0px 22px;
      background: #fff;
      position: absolute; }
  #menu5-main > p {
    width: 100%;
    text-align: center;
    margin-top: 20px;
    line-height: 25px; }

    .menu1-footer {
      width: 100%;
      margin:0 auto;
      padding:70px 0;
      background:#fff;
      position:relative;
      z-index:30;
      text-align: center; }

@media screen and (max-width: 1200px) {
  body {
    width: 1200px; }

  #menu5-banner {
    width: 1200px; } }

/*# sourceMappingURL=menu5.css.map */

</style>



