            <span></span>
            <h5>Q&A</h5>
          </div>
          <ul>
            <li>
              <img src="../../static/myimg7/menu/menu2/q-01.gif">
              <p>A. ジュースは保存料などの添加物を一切使用せず、新鮮な野菜とフルーツで作った生物のためお日持ちはしません。1日分のセットとなりますので、ご購入いただいたその日のうちにお召し上がりください。</p>
            </li>
            <li>
              <img src="../../static/myimg7/menu/menu2/q-02.gif">
              <p>A. 冷蔵保存が必要なため、2時間以上お持ち歩きの場合は、店頭で保冷剤、保冷バッグを販売しておりますでお買い求め下さい。</p>
            </li>
            <li>
              <img src="../../static/myimg7/menu/menu2/q-03.gif">
              <p>A. セットはジュースの効能を考えた組み合わせになっておりますので目的、用途に合わせてお選び下さい。活動量が多い方や、クレンズに慣れていない方には本数が多めのA, Bセットがオススメです。</p>
            </li>
            <li>
              <img src="../../static/myimg7/menu/menu2/q-04.gif">
              <p>A. 個人の生活スタイルによってデトックスの効果を感じる日数には差があります。 半日や1日のクレンズでも、胃を休め、食べ物から意識を離す、心身のデトックス効果が十分にあります。それ以上続けると、体重の減量や肌トーンの向上など見た目にもわかる変化が起きやすくなります。
              </p>
            </li>
            <li>
              <img src="../../static/myimg7/menu/menu2/q-05.gif">
              <p>A. 体内をデトックスする過程で起きる症状で、個人によって度合いが異なります。疲労感、眠気、頭痛、下痢、便秘などが一般的ですが、他にもいつもと体調が違うようなことが起きるかもしれません。ジュースと一緒にたくさんのお水を摂取し、デトックスを促すと次第に症状が和らぐことが多いです。</p>
            </li>
            <li>
              <img src="../../static/myimg7/menu/menu2/q-06.gif">
              <p>A. 前日は軽めの消化が良い食事にするとファスティング時に体への負荷が少なく、またデトックス効果も高まります。回復食には生野菜のサラダや果物、また野菜のスープなど、消化が早く、栄養豊富なメニューがおすすめです。</p>
            </li>
            <li>
              <img src="../../static/myimg7/menu/menu2/q-07.gif">
