<template lang="html">
  <div class="app">
    <div class="box">
        <img src="../../static/myimg7/locations/img-hdr.jpg" />
    </div>
    <div class="content">
        <div class="write">
            <h2>Diary</h2>
            <hr/>
            <br/>
            <p class="jiazai"><a>VIEW  MORE</a></p>
            <p class="jiazai"><span></span>
	<span></span>
	<span></span>
</p>
        </div>
    </div>

  </div>
</template>

<script>
export default {
  mounted(){
    window.onscroll=null;
    $(".jiazai").eq(0).click(function(){

        $(".jiazai").eq(0).css({display:"none"});
        $(".jiazai").eq(1).css({display:"block"})
    })
  }
}
</script>

<style scoped lang="css">
*{
    margin: 0;
    padding: 0;
    list-style: none;
}
html,body{
    width: 100%;
    height: 100%;
}

/*Diar的css样式*/
.box{
    width: 100%;
}
.box img{
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
}
.content{
    position: relative;
    z-index: 2;
    margin-top: 340px;
    height:360px;
    background: #ffffff;
    overflow: hidden;
}
.write{
    width: 100%;
}
.write h2{
    margin-bottom: 40px;
    padding-top: 70px;
    font-size: 56px;
    line-height: 1;
    text-align: center
}
.write hr{
    width:88px;
    margin: 0 auto;
    color: #cccccc;
}
.write p{
    clear: both;
    text-align: center;
    font-size: 18px;
    line-height: 1.0;
    padding-top: 32px;
}
.write p a{
    color: #888888;
    text-decoration: underline;
}
p{
  display:inline-block;
  width:100%;
  text-align:center;
}
.jiazai:nth-child(5){
  cursor: pointer;
  display:none;
}
p>span{
  margin:0 5px;
		width:10px;
		height:10px;
    position:relative;
		background:black;
    display:inline-block;
		border-radius:50%;
}
p>span:nth-child(1){
		animation:hui .4s .1s alternate infinite;
	}
  p>span:nth-child(2){
  		animation:hui .4s .2s alternate infinite;
  	}
    p>span:nth-child(3){
    		animation:hui .4s .3s alternate infinite;
    	}
@keyframes hui{
		from{
			top:0;
		}
		to{
		top:-10px;
		}
	}
</style>



